/* src/components/ImageScrollBox.css */
.scroll-box {
    width: 300px;
    height: 400px;
    overflow-y: scroll;
    border: 2px solid #ffdc9b;
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(70px, 1fr));
    gap: 10px;
  }
  
  /* Custom scrollbar styles */
  .scroll-box::-webkit-scrollbar {
    width: 16px; /* Adjust the width of the scrollbar */
  }
  
  .scroll-box::-webkit-scrollbar-thumb {
    background-color: #3d1900; /* Color of the scrollbar thumb */
    border-radius: 10px; /* Rounded corners for the scrollbar thumb */
  }
  
  .scroll-box::-webkit-scrollbar-thumb:hover {
    background-color: #3d1900; /* Darker brown when hovering over the scrollbar thumb */
  }
  
  .scroll-box::-webkit-scrollbar-track {
    background-color: #ffdc9b; /* Background color of the scrollbar track */
  }
  
  .scroll-image {
    /* width: 100%; */
    max-width: 100%;
    /* width:100%; */
  }
  