

/* TerrainFeature.css*/

.terrain-feature {
    position: absolute;
    width: 100px;
    height: 100px;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 0; /* Adjust z-index if necessary */
    top:0;
    left:0;
    image-rendering: optimizeSpeed;             /* Older versions of Firefox */
    image-rendering: -moz-crisp-edges;          /* Firefox */
    image-rendering: -o-crisp-edges;            /* Opera */
    image-rendering: -webkit-optimize-contrast; /* Safari and Chrome */
    image-rendering: pixelated;                 /* Chrome and Opera */
    image-rendering: crisp-edges;               /* CSS4 draft specification */
}